.main-header-container {
  height: 100rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1rem solid #c3c3c3;
  // padding: 0rem 17rem;
  background-color: #fd7e14;

  .top-bar {
    .right,
    .left {
      width: 50%;
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      p {
        font-size: 14px;
        color: white;
      }
    }
  }

  .header {
    width: 100%;
    padding-right: 12px;
    padding-left: 12px;
    margin-right: auto;
    margin-left: auto;
    display: flex;
    justify-content: center;
    position: relative;
    padding-bottom: 20px;
  }

  .brand-name {
    font-family: "Outfit", sans-serif;
    color: white;
    width: 100px;
    font-size: 16rem;
    padding-bottom: 10px;
  }

  @media (min-width: 576px) {
    .header {
      max-width: 540px;
    }
  }

  @media (min-width: 768px) {
    .header {
      max-width: 720px;
    }
  }

  @media (min-width: 992px) {
    .header {
      max-width: 960px;
    }
  }

  @media (min-width: 1200px) {
    .header {
      max-width: 1200px;
    }
  }

