.product-starbuyer-item-container {
  // padding: 10px;
  cursor: pointer;
  // background: #e6e6e6;
  // margin: 0 5px;
  margin-bottom: 30px;
  padding: 10px 0px;

  &:hover {
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
  }

  p {
    margin: 0;
  }
  h3 {
    margin: 10px 0;
  }
  .field-product-starbuyer {
    font-size: 14px;
    font-weight: 500;
    // margin: 5px 0;
    display: flex;
    justify-content: space-between;
    // height: 25px;
    align-items: center;
    margin-bottom: 10px;
  }
  img {
    max-width: 100%;
    max-height: 100%;
  }

  .title-product-starbuyer {
    color: #2783ef;
    font-size: 16px;
    cursor: pointer;

    &:hover {
      opacity: 0.6;
    }
  }
}
